.rsc-container {
  &.is-flex {
    display: flex;
  }
  &.flex-h {
    flex-direction: row;
  }
  &.flex-v {
    flex-direction: column;
  }
  &.flex-grow {
    flex-grow: 1;
  }
  &.is-aligned-center {
    align-items: center;
  }
  &.is-justified-center {
    justify-content: center;
  }
}
